html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-size: cover;
  position: relative;
  font-family: "Roboto", sn-serif !important;
}

#panel {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

p {
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4 {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}

.container {
  max-width: 1010px;
  padding: 0 20px;
  width: 100%;
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.footer {
  background: #333333;
  padding: 10px 0;
}

.footer p {
  color: #ffffff;
}

.footer a {
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
}

.footer__social a {
  width: 25px;
  height: 25px;
  background: #606060;
  display: inline-block;
  border-radius: 160px;
  border: none;
}

.slideout-menu {
  position: fixed;
  z-index: 8;
  top: 0;
  bottom: 0;
  display: none;
  overflow-y: scroll;
  width: 256px;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  background: #181a22;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}

.slideout-menu::-webkit-scrollbar {
  width: 0;
}

.slideout-menu-right {
  right: 0;
  overflow-x: hidden;
}

.slideout-menu-right ul {
  list-style: none;
  padding-left: 10px;
  padding-top: 35px;
  position: relative;
}

.slideout-menu-right li {
  padding-top: 35px;
  padding-left: 40px;
}

.slideout-menu-right a {
  color: #cdcdcd;
  font-size: 20px;
  font-weight: 100;
  transition: all 0.3s;
}

.slideout-menu-right .user ul {
  padding-top: 5px;
}

.slideout-menu-left {
  left: 0;
}

.slideout-panel {
  position: relative;
  z-index: 9;
  min-height: 100vh;
  background-color: #fff;
  will-change: transform;
}

.slideout-open {
  overflow: hidden;
}

.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}

.panel:before {
  content: '';
  display: block;
  background-color: transparent;
  transition: background-color 0.5s ease-in-out;
}

.panel-open:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

#btn {
  display: block;
  width: 36px;
  height: 20px;
  padding-top: 20px;
  cursor: pointer;
}

#btn span {
  position: relative;
  display: block;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn span:after, #btn span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
}

#btn span:after {
  top: 10px;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn span:before {
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn.on span {
  background-color: transparent;
}

#btn.on span:before {
  transform: rotate(45deg) translate(6px, 5px);
}

#btn.on span:after {
  transform: rotate(-45deg) translate(9px, -8px);
}

#btn.on {
  display: none;
}

.menu-close {
  position: absolute;
  top: 20px;
  right: 10%;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.menu-close span {
  background-color: transparent;
  position: relative;
  top: 20px;
}

.menu-close span:after, .menu-close span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
}

.menu-close span:after {
  content: '';
  top: 10px;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
  transform: rotate(-45deg) translate(9px, -8px);
}

.menu-close span:before {
  content: '';
  transform: rotate(45deg) translate(6px, 5px);
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.top-line-anchor {
  display: inline-block;
}

@media (max-width: 767px) {
  .top-line-anchor {
    left: 20px;
  }
}

.top-line-anchor {
  display: none;
  position: absolute;
  left: 10px;
  top: 10px;
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
}

.header__toolbar {
  padding: 15px 0;
}

.header__toolbar a {
  display: block;
  color: #ffffff;
}

.header__content {
  background: #ffffff;
  padding: 10px 0;
}

.header__content a {
  color: #000000;
}

.header__content__tel a {
  background: #45279f;
  display: inline-block;
  padding: 10px 23px;
}

.header__content__tel a span {
  color: #ffffff;
  font-size: 27px;
}

.header__content__tel a p {
  color: #ffffff;
}

.trademark {
  background: url("http://via.placeholder.com/1000x900") no-repeat;
  width: 100%;
  height: 700px;
  background-size: cover;
}

.trademark .container, .trademark .row {
  height: 100%;
}

.trademark__content {
  max-width: 600px;
}

.trademark__content h1 {
  color: #ffffff;
  padding-top: 120px;
}

.trademark__content p {
  color: #ffffff;
  padding: 25px 0;
}

.trademark__content a {
  display: inline-block;
  padding: 10px 35px;
  border: 2px solid #ffffff;
  border-radius: 4px;
  color: #ffffff;
  transition: all 0.4s;
}

.trademark__content a:hover {
  border-color: #45279f;
  color: #45279f;
}

.advantages h2 {
  color: #000000;
  font-size: 38px;
  padding-top: 40px;
}

.advantages ul {
  padding-bottom: 60px;
  padding-top: 30px;
}

.advantages li {
  position: relative;
  font-size: 15px;
  line-height: 30px;
  padding-left: 50px;
  padding-right: 30px;
  color: #606060;
}

.advantages li span {
  width: 6px;
  height: 6px;
  display: inline-block;
  background: #45279f;
  border-radius: 160px;
  position: absolute;
  left: 30px;
  top: 13px;
}

.advantages__img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-image: url("http://via.placeholder.com/150x150");
}

.advantages .col-xs-6 {
  padding: 0;
}

.command {
  padding: 50px 0;
}

.command h2 {
  color: #000000;
  font-size: 38px;
}

.command__block {
  margin-top: 40px;
  height: 400px;
  background-image: url("http://via.placeholder.com/300x300");
  margin-left: 20px;
  position: relative;
  background-size: cover;
}

.command__block--padding {
  margin-right: 20px;
  margin-left: 0;
}

.command__block__name {
  position: absolute;
  width: 100%;
  bottom: 0;
  background: rgba(69, 39, 159, 0.6);
  padding: 10px 0;
}

.command__block__name p {
  font-size: 20px;
}

.command__block__name span {
  font-size: 14px;
}

.tariff {
  padding-top: 30px;
}

.tariff h2 {
  color: #000000;
  font-size: 38px;
  padding-bottom: 30px;
}

.tariff .col-xs-3 {
  margin: 0 20px;
}

.tariff__block {
  border: 2px solid #45279f;
  border-radius: 4px;
  min-height: 350px;
  position: relative;
  padding-bottom: 110px;
}

.tariff__block h4 {
  padding: 15px 0;
  width: 100%;
  background: #45279f;
  margin: 0 auto;
  justify-content: center;
  display: flex;
  color: #ffffff;
}

.tariff__block ul {
  padding-top: 20px;
}

.tariff__block li {
  padding-left: 50px;
  line-height: 30px;
  font-size: 15px;
}

.tariff__block__bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
  padding-bottom: 20px;
}

.tariff__block__bottom span {
  display: block;
  padding-bottom: 20px;
}

.tariff__block__bottom a {
  display: inline-block;
  padding: 10px 43px;
  background: #45279f;
  color: #ffffff;
}

.trust {
  position: relative;
}

.trust h2 {
  color: #000000;
  font-size: 38px;
  padding-top: 40px;
}

.trust .swiper-container {
  width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 50px 35px;
}

.trust .swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100px;
}

.trust .swiper-slide img {
  position: relative;
  z-index: 9;
}

.trust .swiper-slide:hover .swiper-slide__block {
  display: block;
}

.trust .swiper-slide:hover .swiper-slide__block--top {
  display: block;
}

.trust .swiper-slide__block {
  display: none;
  position: absolute;
  top: 125px;
  background: #45279f;
  z-index: 10;
  border-radius: 4px;
  padding: 10px;
  width: 200px;
}

.trust .swiper-slide__block p {
  font-size: 15px;
  color: #ffffff;
  font-weight: 200;
}

.trust .swiper-slide__block p:first-child {
  font-size: 20px;
  font-weight: 500;
}

.trust .swiper-slide__block:after {
  content: '';
  position: absolute;
  top: -20px;
  left: 45%;
  border: 10px solid transparent;
  border-bottom: 10px solid #45279f;
}

.trust .swiper-slide__block--top {
  display: none;
  position: absolute;
  background: #45279f;
  z-index: 10;
  border-radius: 4px;
  padding: 10px;
  width: 200px;
  bottom: 120px;
}

.trust .swiper-slide__block--top p {
  font-size: 15px;
  color: #ffffff;
  font-weight: 200;
}

.trust .swiper-slide__block--top p:first-child {
  font-size: 20px;
  font-weight: 500;
}

.trust .swiper-slide__block--top:after {
  content: '';
  position: absolute;
  left: 45%;
  bottom: -20px;
  border: 10px solid transparent;
  border-top: 10px solid #45279f;
}

.trust .swiper-container-android .swiper-slide, .trust .swiper-wrapper {
  transform: none;
}

.what {
  padding: 100px;
}

.what h2 {
  color: #000000;
  font-size: 38px;
  padding-bottom: 20px;
}

.what a {
  color: #606060;
  border-bottom: 1px solid #606060;
  margin-bottom: 50px;
  display: inline-block;
}

.what__block {
  padding-bottom: 30px;
}

.whom {
  background: #45279f;
  padding-top: 60px;
  padding-bottom: 100px;
}

.whom h2 {
  color: #000000;
  font-size: 38px;
  color: #ffffff;
  padding-bottom: 70px;
}

.whom p {
  color: #ffffff;
  max-width: 200px;
