.header {
  position: absolute;
  top: 0;
  width: 100%;
  &__toolbar {
    padding: 15px 0;
    a {
      display: block;
      color: $white; } }
  &__content {
    background: $white;
    padding: 10px 0;
    a {
      color: $black; }
    &__tel {
      a {
        background: $purple;
        display: inline-block;
        padding: 10px 23px;
        span {
          color: $white;
          font-size: 27px; }
        p {
          color: $white; } } } } }
